	module.exports = {"data":{"challengeNode":{"title":"Add Placeholder Text to a Text Field","description":["<code>Placeholder</code>占位符是用户在<code>input</code>输入框中输入任何东西前的预定义文本。","你可以像这样创建一个占位符：","<code>&#60;input type=\"text\" placeholder=\"this is placeholder text\"&#62;</code>","<hr>","把<code>input</code>输入框的<code>placeholder</code>占位符文本设置为 \"猫咪图片地址\"。"],"challengeType":0,"fields":{"slug":"/responsive-web-design/basic-html-and-html5/add-placeholder-text-to-a-text-field","blockName":"Basic HTML and HTML5","tests":[{"text":"给现有的<code>input</code>输入框添加一个<code>placeholder</code>属性。","testString":"assert($(\"input[placeholder]\").length > 0, '给现有的<code>input</code>输入框添加一个<code>placeholder</code>属性。');"},{"text":"设置<code>placeholder</code>属性的值为 \"猫咪图片地址\"。","testString":"assert($(\"input\") && $(\"input\").attr(\"placeholder\") && $(\"input\").attr(\"placeholder\").match(/猫咪图片地址/gi), '设置<code>placeholder</code>属性的值为 \"猫咪图片地址\"。');"},{"text":"<code>input</code>输入框的语法必须正确。","testString":"assert($(\"input[type=text]\").length > 0 && code.match(/<input((\\s+\\w+(\\s*=\\s*(?:\".*?\"|'.*?'|[\\^'\">\\s]+))?)+\\s*|\\s*)\\/?>/gi), '<code>input</code>输入框的语法必须正确。');"}]},"required":[],"files":{"indexhtml":{"key":"indexhtml","ext":"html","name":"index","contents":"<h2>CatPhotoApp</h2>\n<main>\n  \n  <a href=\"#\"><img src=\"http://cdn.freecodecamp.cn/relaxing-cat.jpg\" alt=\"一只仰卧着的萌猫\"></a>\n  \n  <p>猫咪最喜欢的三件东西：</p>\n  <ul>\n    <li>猫薄荷</li>\n    <li>激光笔</li>\n    <li>千层饼</li>\n  </ul>\n  <p>猫咪最讨厌的三件东西：</p>\n  <ol>\n    <li>跳蚤</li>\n    <li>打雷</li>\n    <li>同类</li>\n  </ol>\n  <input type=\"text\">\n</main>","head":"","tail":""},"indexjs":null,"indexjsx":null}}},"pathContext":{"challengeMeta":{"introPath":"","template":null,"required":[],"nextChallengePath":"/responsive-web-design/basic-html-and-html5/create-a-form-element","id":"bad87fee1348bd9aedf08830"},"slug":"/responsive-web-design/basic-html-and-html5/add-placeholder-text-to-a-text-field"}}

